import xflSelect from '../../../components/xfl-select/xfl-select.vue'
var  graceChecker = require("../../../common/graceChecker.js");
export default {
	data() {
		return {
			PageCur: '我的',
			TabCur: 0,
			scrollLeft: 0,
			imgList: [],
			switchA: false,
			switchB: true,
			switchC: false,
			switchD: false,
			radio: 'A',
			checkbox: [{
				value: 'A',
				checked: false
			}, {
				value: 'B',
				checked: true
			}, {
				value: 'C',
				checked: false
			}],
			list1: [
			   '大安街道',
			   '凉高山街道',
			   '龙井街道',
			   '马冲口街道',
			],
			list2: [
			   '华大社区',
			   '大楻桶社区',
			   '广华社区',
			   '红苕地社区',
			],
			list3: [
			   '华大小区',
			   '大楻桶小区',
			   '广华小区',
			   '红苕地小区',
			],
		
		}
	},
	components: { xflSelect },  //注册为子组件
	methods: {
		NavChange: function(e) {
			this.PageCur = e.currentTarget.dataset.cur
		},
		tabSelect(e) {
			this.TabCur = e.currentTarget.dataset.id;
			this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60
		},
		goMore(url) {
			uni.navigateTo({
				url: url
			});
		},
		PickerChange(e) {
			this.index = e.detail.value
		},
		SwitchA(e) {
			this.switchA = e.detail.value
		},
		SwitchB(e) {
			this.switchB = e.detail.value
		},
		SwitchC(e) {
			this.switchC = e.detail.value
		},
		SwitchD(e) {
			this.switchD = e.detail.value
		},
		
		CheckboxChange(e) {
			var items = this.checkbox,
				values = e.detail.value;
			for (var i = 0, lenI = items.length; i < lenI; ++i) {
				items[i].checked = false;
				for (var j = 0, lenJ = values.length; j < lenJ; ++j) {
					if (items[i].value == values[j]) {
						items[i].checked = true;
						break
					}
				}
			}
		},
		formSubmit: function(e) {
			console.log('form发生了submit事件，携带数据为：' + JSON.stringify(e.detail.value))
		    //定义表单规则
		    var rule = [
			    {name:"user", checkType : "string", checkRule:"1,8",  errorMsg:"用户名为必填项"},
				{name:"country", checkType :"notnull", checkRule:"1,50",  errorMsg:"加*的下拉列表为必选项"},
				{name:"country", checkType : "notnull", checkRule:"1,50",  errorMsg:"加*的下拉列表为必选项"},
				{name:"country", checkType : "notnull", checkRule:"1,50",  errorMsg:"加*的下拉列表为必选项"},
				{name:"nickname", checkType : "string", checkRule:"1,4",  errorMsg:"姓名应为1-4个字符"},
				{name:"num", checkType : "phoneno", checkRule:"1,50",  errorMsg:"手机号码为必填项"},
				{name:"input", checkType : "string", checkRule:"1,50",  errorMsg:"验证码为必选项"},
				{name:"agreement", checkType : "notnull",  checkRule:"",  errorMsg:"请同意勾选协议"},
		    ];
		    //进行表单检查
		    var formData = e.detail.value;
		    var checkRes = graceChecker.check(formData, rule);
		    if(checkRes){
		        uni.showToast({title:"验证通过!", icon:"none"});
		    }else{
		        uni.showToast({ title: graceChecker.error, icon: "none" });
		    }
		},
	}
}
